<script>
import "@/views/ig.js";

// import "https://www.instagram.com/embed.js";
import axios from "axios";
import { onMounted, ref } from "vue";
export default {
  setup() {
    let posts = ref();

    let getPosts = async () => {
      let temp = [];
      let resp = await axios.get(`https://crtapi.bignetlab.com/instagram`, {
        headers: {
          token: `adaptivenetlabsss`,
        },
      });

      temp.push({ link: resp.data.data[0].url1 });
      temp.push({ link: resp.data.data[0].url2 });
      temp.push({ link: resp.data.data[0].url3 });

      posts.value = temp;
    };
    onMounted(async () => {
      await getPosts();
      await window.instgrm.Embeds.process();
    });

    return {
      posts,
    };
  },
};
</script>
