<script>
import "@/views/ig.js";
import { computed, reactive, onMounted, ref } from "vue";
import Flicking from "@egjs/vue3-flicking";
import { Arrow, Fade, AutoPlay } from "@egjs/flicking-plugins";
import Feed from "@/views/Home/Feed";

import "vue3-carousel/dist/carousel.css";
import { Carousel, Slide, Pagination, Navigation } from "vue3-carousel";

import AOS from "aos";
import "aos/dist/aos.css";
import axios from "axios";
export default {
  components: {
    Flicking: Flicking,
    Feed,
    Carousel,
    Slide,
    Pagination,
    Navigation,
  },
  setup() {
    AOS.init({
      delay: 400, // values from 0 to 3000, with step 50ms
      duration: 400, // values from 0 to 3000, with step 50ms
      easing: "ease-in-out-sine",
      once: true,
    });
    let blogs = ref([]);
    let data = reactive({
      model: 0,
      plugins: [new Arrow(), new Fade("", 1.7), new AutoPlay(2000, "NEXT")],
      breakpoints: {
        // 700px and up
        700: {
          itemsToShow: 2.5,
          snapAlign: "center",
        },
      },
      kegiatan: [
        { nama: "NetSchool", src: "./img/activity/netschool.png" },
        { nama: "NetShow", src: "./img/activity/netshow.png" },
        { nama: "NetShare", src: "./img/activity/netshare.png" },
        { nama: "NetCompetition", src: "./img/activity/netcompt.png" },
        { nama: "NetNongkrong", src: "./img/activity/netnongkrong.png" },
        { nama: "NetDev", src: "./img/activity/netshare.png" },
      ],
      articles: null,

      aslab_otm: [
        {
          name: "Gilang Virga Perdana",
          award: "Practicum Assistant of The Month",
          img: "./img/aslabhome/GILANG.png",
        },
        {
          name: "Kania Pradnya Sekardefi",
          award: "Practicum Assistant of The Month",
          img: "./img/aslabhome/KANIA.png",
        },
        {
          name: "Mochammad Iqbal Fahriza",
          award: "Practicum Assistant of The Month",
          img: "./img/aslabhome/IQBAL.png",
        },
      ],
      aslab: [
        {
          nama: "Peiter Solarso Pasaribu",
          jabatan: "Koordinator Asisten",
          src: "./img/aslabhome/PIETER.png",
        },
        {
          nama: "Regin Georgius Sembiring",
          jabatan: "Koordinator Praktikum",
          src: "./img/aslabhome/REGIN.png",
        },
        {
          nama: "Dimas Pratama",
          jabatan: "Koordinator SDM & Internal",
          src: "./img/aslabhome/DIMAS.png",
        },
        {
          nama: "Ahmad Naoval Annasik",
          jabatan: "Koordinator Riset & Eksternal",
          src: "./img/aslabhome/NOVAL.png",
        },
      ],
    });

    let filActivity = computed(() => {
      let temp = [];
      if (data.model > 1) {
        data.model = 0;
      } else if (data.model < 0) {
        data.model = 1;
      }
      for (let i = data.model * 3; i < data.model * 3 + 3; i++) {
        temp.push(data.kegiatan[i]);
      }

      return temp;
    });

    let fetchBlog = async () => {
      let resp = await axios.get(
        "https://blog.adaptivenetworklab.org/wp-json/wp/v2/posts"
      );

      blogs.value = resp.data;
      if (blogs.value.length > 8) {
        blogs.value = resp.data.slice(0, 8);
      }

      // get author name
      for (let idx = 0; idx < blogs.value.length; idx++) {
        let resp = await axios.get(blogs.value[idx]._links.author[0].href);
        blogs.value[idx].authorName = resp.data.name;
      }

      // get image
      for (let idx = 0; idx < blogs.value.length; idx++) {
        let resp = await axios.get(
          blogs.value[idx]._links["wp:featuredmedia"][0].href
        );
        blogs.value[idx].img = resp.data.guid.rendered;
      }
    };

    let asistent = ref();
    let getAsistent = async () => {
      let resp = await axios.get(`https://crtapi.bignetlab.com/asistent`, {
        headers: {
          token: `adaptivenetlabsss`,
        },
      });

      for (let item of resp.data.data) {
        item.img = `https://crtapi.bignetlab.com/storage/avatar/${item.foto}`;
      }

      data.aslab_otm = resp.data.data;
    };

    onMounted(async () => {
      await getAsistent();
      await fetchBlog();
    });

    let truncate = (text) => {
      if (text.length >= 45) {
        text = text.substring(0, 42) + "...";
      }
      return text;
    };

    return {
      data,
      filActivity,
      blogs,
      truncate,
      asistent,
    };
  },
};
</script>
